
.income{background-color:#eeeeee;padding-bottom:40px
}
.income .vux-header{background-color:rgba(46,147,230,0.95);border-color:rgba(46,147,230,0.95);color:#fff
}
.income .vux-header-title{color:#fff
}
.income .vux-header .vux-header-left .left-arrow:before{border-color:#fff
}
.income header{position:relative;background-color:#2e93e6;color:#fff;padding:40px 20px;text-align:center
}
.income header .cash{font-size:15px
}
.income header .cash p span{font-size:30px
}
.income header .tip{left:0;right:0;position:absolute;bottom:8px;font-size:12px
}
.income .weui_btn:after{border-radius:0;border:none
}
.income .icon-11{color:#fff;font-size:12px;vertical-align:middle;margin:0 5px
}
.income .flexbox-item{background-color:#fff;line-height:45px;text-align:center
}
.income .flexbox-item a{color:#000
}
.income .flexbox{padding:0;border-bottom:solid 1px #d5d5d5
}
.income .cell_value{color:#68709a
}
.income .weui_cell_ft.with_arrow:after{border-color:#68709a
}
.border-right{border-right:solid 1px #e2e2e7
}
.chart_content{margin:15px 0;padding:15px;background-color:#fff
}
.chart{background-color:#fff
}


