
@import '//at.alicdn.com/t/font_c3y7a0wfn0dd9529.css';
html{-ms-text-size-adjust:100%;-webkit-text-size-adjust:100%
}
body{line-height:1.6;font-family:"Helvetica Neue",Helvetica,Arial,sans-serif
}
*{margin:0;padding:0
}
a img{border:0
}
a{text-decoration:none
}
body{background-color:#f2f3f8
}
a{color:#007de2
}
div,p,a,span,ul,li,img{box-sizing:border-box
}
.iconfont{color:#007de2
}
button.weui_btn_primary{background-color:#007de2
}
button.weui_btn_plain_primary{color:#007de2;border:1px solid #007de2
}
button.weui_btn_primary:not(.weui_btn_disabled):active{background-color:#007de2
}
input::-webkit-input-placeholder{color:#68709a
}
.vux-header{border-bottom:solid 1px #dee1ed
}
.vux-header-title{font-size:16px
}
.vux-header-right{font-size:15px
}


