
.filter{padding-bottom:70px
}
main{font-size:12px
}
.filter .flexbox{padding:15px 0
}
.filter .flexbox-item{text-align:center;border-right:solid 1px #d5d5d5
}
.filter .flexbox-item:last-child{border-right:0
}
.filter footer{position:fixed;left:0;right:0;bottom:0;padding:10px;background-color:#fff
}
.filter .checker-item{width:70px;line-height:30px;font-size:12px;text-align:center;border-radius:3px;border:1px solid #d4d4d7;background-color:#fff;margin-right:6px
}
.filter .checker-item-selected{background:#d7edff url(../../../static/img/checker.png) no-repeat right top;background-size:14px 13px;border-color:#007de2;color:#007de2
}
.filter .group .weui_cells{padding:15px;padding-top:0
}
.filter .filter-title{font-size:12px;line-height:40px
}


